import { Logger } from '@transai/logger';
import {
  BaseConnectorConfig,
  KafkaCallbackResponse,
  XodBaseMessageType,
} from '@xip-online-data/types';

import { BadRequest } from './factories';
import { MessageMonitorInterface } from './message-monitor.interface';

export abstract class AbstractMessageMonitor
  implements MessageMonitorInterface
{
  constructor(protected readonly baseConfig: BaseConnectorConfig) {}

  protected abstract isProcessed(message: XodBaseMessageType): Promise<boolean>;

  protected abstract addProcessedMessage(
    message: XodBaseMessageType,
  ): Promise<void>;

  public processMessage(
    callbackFunction: (
      message: XodBaseMessageType,
    ) => Promise<KafkaCallbackResponse>,
  ): (message: XodBaseMessageType | any) => Promise<KafkaCallbackResponse> {
    return async (message: XodBaseMessageType) => {
      /**
       * Check if message has already been processed
       *
       * Potential race-condition here if two messages with the same eventId are processed at the same time
       * This is a known issue and is acceptable for the current use-case since the messages will arrive
       * with a few seconds to a few minutes delay (minimal delay found was 10 seconds).
       *
       * Async is required for the Redis implementation to work
       *
       */
      if (await this.isProcessed(message)) {
        Logger.getInstance().debug(
          `Message ${message.eventId} already processed`,
        );
        return BadRequest(`Message ${message.eventId} already processed`)(
          message,
        );
      }
      Logger.getInstance().debug(`Processing message ${message.eventId}`);

      const result = await callbackFunction(message);

      if (result.success) {
        await this.addProcessedMessage(message);
      } else {
        Logger.getInstance().debug(
          `Message ${message.eventId} FAILED to process: ${result.message}. Not storing in processed messages list.`,
        );
      }

      return result;
    };
  }
}
